import "./LoginPage.css"
import GooglePng from '../img/google.png';
const LoginPage = () => {
    return ( 
        <div className="loginContainer">
        <h1>Warsaw Night Racing</h1>
      <a href={`/auth/google`}>
        <div className="loginGoogle">

          <img
            src={GooglePng}
            alt="Google"
          />
          Login with Google
        </div>
      </a>

      </div>

     );
}
 
export default LoginPage;